body{
  background-color: #333;
  color: white;
  text-align: center;
}

video{
  width: 100%;
  height: 500px;
}

img{
  width: 100%;
  height: 500px;
}

ul{
  list-style: none;
}

li a{
  display: inline-block;
  padding: 10px;
}

footer a{
  color: white;
}

#icon{
  height: 40px;
  width: 40px;
}

#paragraph{
  padding-right: 10px;
}

.centered{
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
}

.left{
  text-align: left;
  padding: 20px;
  padding-left: 10px;
  background-color: #242e40;
  color: white;
}

.center{
  color: white;
  background-color: #242e40;
  overflow: hidden;
}